body {
  display: inline-block;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.Wrapper {
  display: grid;
  grid-template-columns: 400px minmax(10px, 1fr);
  grid-gap: 1rem;
  align-items: start;
}

aside {
  position: sticky;
  top: 0;
  margin-top: 1em;
  margin-left: 1em;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}




.asideHeader,
.asideRow {
  text-align: center;
}

.asideHeader,
.asideRow,
.asideHeaderPlayer,
.asideRowPlayer {
  padding: 0.2em 1em;
  border: 1px solid #dee2e6;
}

main input {
  margin-block: 1em;
}

main div.p-datatable {
  margin-right: 1em;
}

a.itemLink:visited,
a.itemLink:active,
a.itemLink {
  text-decoration: none;
  color: #282c34;
}